input {
    width: 40%;
    min-width: 200px;
    border-radius: 10px;
    border: 2px solid #14877c;
    padding: 10px;
    font-size: 14px;
}

select {
    width: 20%;
    margin-left: 1rem;
    min-width: 50px;
    border-radius: 10px;
    border: 2px solid #14877c;
    padding: 10px;
    font-size: 14px;
}

